import React from 'react';

export class Memory extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            remaining: navigator.deviceMemory * 1024,
            allocated: 0,
            allocationindex: 0,
        };
        this.blockSize = 0;
        this.memalloc = []
        this.stopAt = 512;
    }
    getMemory() {
        return (navigator.deviceMemory * 1024).toString() + " MB"
    }


    addElement() {
        let arr = []
        for (var i=0;i<1000;i++) {
            arr.push(i/10)
        }
        
        if(this.blockSize===0) {
            this.blockSize = memorySizeOf(arr)
        }
        this.memalloc[this.state.allocationindex] = arr
    }

    tick() {
        this.addElement()
        this.setState(state => ({
            allocated: state.allocated + this.blockSize,
            remaining: state.remaining - this.blockSize,
            allocationindex: state.allocationindex + 1,
        }))
        if (this.state.remaining < this.blockSize || this.state.allocated > this.stopAt) { clearInterval(this.interval) }
    }

    componentDidMount() {
        this.interval = setInterval(() => this.tick(), 5);
    }

    componentWillUnmount() {
        clearInterval(this.interval);
    }

    render() {
        var memory = this.getMemory();
        return (
            <div className="MemoryStat">Memory: {memory}
                 <MemTest allocated={this.state.allocated} remaining={this.state.remaining}/></div>
       
        )
    }
}


const MemTest = ({allocated, remaining}) => {
    var allocatedString = allocated.toFixed(3)
    var remainingString = remaining.toFixed(3)
    return (
        <div>
        <span className="memtest">Allocated: {allocatedString}MB Estimated Remaining: {remainingString}MB
        </span>
        </div>
    )
    
}

// https://gist.github.com/zensh/4975495
function memorySizeOf(obj) {
    var bytes = 0;

    function sizeOf(obj) {
        if(obj !== null && obj !== undefined) {
            switch(typeof obj) {
            case 'number':
                bytes += 8;
                break;
            case 'string':
                bytes += obj.length * 2;
                break;
            case 'boolean':
                bytes += 4;
                break;
            case 'object':
                var objClass = Object.prototype.toString.call(obj).slice(8, -1);
                if(objClass === 'Object' || objClass === 'Array') {
                    for(var key in obj) {
                        if(!obj.hasOwnProperty(key)) continue;
                        sizeOf(obj[key]);
                    }
                } else bytes += obj.toString().length * 2;
                break;
            default:
                console.log("Unhandled type: " + typeof obj)
            }
        }
        return bytes;
    };

    function formatByteSize(bytes) {
        return (bytes / 1024 / 1024)
    };

    return formatByteSize(sizeOf(obj));
};