import React from 'react';

import * as drmConstantClass from './const.js'
import * as defaultEme from './defaulteme.js'
import * as webkitEme from './webkiteme.js'
import * as msEme from './mseme.js'
import * as constants from './const.js'
import {PlayReady}  from './playready.js'

// EME is the primary EME e.g. MS, EME, WebKit
export class EME extends React.Component {
    render() {
        const EMEs = drmConstantClass.EMEs
        return (
            <div className="EME" key="EME">
                {EMEs.map((name) => (
                <DRMType EME={name} key={name}/>
            ))}
            <PlayReady/>
            </div>
        )
    }
}

class DRMType extends React.Component {
    render() {

        return (
           <div className="DRMKeySystem" 
            id={this.props.keySystem}
            key={this.props.EME + "-" + this.props.keySystem}>{this.props.EME}
           <DRMKeySystem EME={this.props.EME}/>
           </div>
        )
    }
}

class DRMKeySystem extends React.Component {
    render() {
        const keySystems = drmConstantClass.keySystems
        return (
            <div>
            {Object.keys(keySystems).map((key) => {
                return (
                    <div id={key} key={this.props.EME+"-" +key}>
                    {keySystems[key].map((subKey) => (
                        <DRMSubType EME={this.props.EME} subKey={subKey}  key={this.props.EME + "-" + key + "-"+ subKey}/>
                    ))}
                    </div>
                )
            })}
            </div>
        )
    }
}


// DRMSubType such as org.widevine.alpha
class DRMSubType extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            colour: 'black',
        };
        this.setSubSystem = this.setSubSystem.bind(this);
    }

    setSubSystem(status) {
        var colour
        (status) ? colour = 'green' : colour = 'red';
        this.changeColour(colour)
    }

    componentDidMount() {
        if (this.props.EME==="Default EME") {
         defaultEme.checkKeySystem(this.setSubSystem, this.props.EME, this.props.subKey, constants.dashconfig)
        }
        if (this.props.EME === "Webkit EME") {
            webkitEme.checkKeySystem(this.setSubSystem, this.props.EME, this.props.subKey, 'video/mp4') 
        }
        if (this.props.EME === "MS EME") {
            msEme.checkKeySystem(this.setSubSystem, this.props.EME, this.props.subKey)
        }
    }        
 
    changeColour(colour) {
        this.setState({colour: colour})
    }

    render() {
        return (
            <span id={this.props.subKey} className="drmSubKey"
             style={{color: this.state.colour}}>{this.props.subKey}</span>
        )
    }
}